$border-color: #ddd;
$input-border: #ccc;
$primary-color: #007bff;
$hover-color: #0056b3;
$base-spacing: 10px;

.card-container {
  position: absolute;
  margin-left: 70%;
  margin-top: -490px;
  min-width: 300px;
  font-size: small;
}

.card {
  box-sizing: content-box;
  width: 200px;
  // margin: 20px;
  // margin-top: 0px;
  // padding: 5px;
  margin: $base-spacing auto;
  padding: $base-spacing;
  border: 2px solid $border-color;
  border-radius: 6px;
}
.img_card{
  width: 200px !important;
  
}

.img_card{
  width: inherit;
  // height: 150px;
}

@media (max-width: 720px){
  .card-container {
    max-width: 300px;
    position: relative;
    margin: 20px;
  }
}
